import React from 'react';
import Slide from '@material-ui/core/Slide';

import Contact from '@material-ui/icons/ContactPhone';
import CloseIcon from '@material-ui/icons/Close';

export class HalamanResume extends React.Component{	
	constructor(props){
		super(props);
		this.handleClose = this.handleClose.bind(this);
	}

	handleClose = () => {
		this.props.back();
	}

	render(){
		const width = this.props.width;
		var fontSizeTitle, fontSizeTitleBg;
		var fontSpacingTitleBg;

		switch(true) {
			case (width <= 480):
				fontSizeTitleBg = '55px';
				fontSpacingTitleBg = 4;
				fontSizeTitle = '25px';
				break;
			case (width <= 600):
				fontSizeTitleBg = '65px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '35px';
				break;
			case (width <= 800):
				fontSizeTitleBg = '80px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '35px';
				break;
			case (width <= 1920):
				fontSizeTitleBg = '95px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '40px';
				break;
			default:
				break;
		}

		const styles = {
			main: {
				position: 'absolute',
				top: 0,
				left: 0,
				height: '100%',
				width: '100%',
				boxSizing: 'border-box',
				overflow: 'hidden',
				background: '#222'
			},
			box: {
				position: 'relative',
				width: '100%',
				height: '100%',
				opacity: 1,
				display: 'flex',
				alignItems: 'center',
				textAlign: 'center',
				fontFamily: 'Poppins,sans-serif,Calibri,Arial'
			},
			centerAlign: {				
				margin: '0 auto',
				width: '100%',
				position: 'relative',
				textAlign: 'center'
			},			
			headerSection: {
				width: '100%',
				letterSpacing: 1,
				margin: '0 auto',
				fontSize: `${fontSizeTitle}`,
				position: 'relative',
				fontWeight: 900,
				textTransform: 'uppercase',
				textAlign: 'center',
				overflow: 'hidden',
				display: 'block',
				lineHeight: `${(width <= 800 ? '145%' : '110%')}`,
			},
			titleBg: {
				color: 'rgba(255, 255, 255, 0.07)',
				fontSize: `${fontSizeTitleBg}`,
				left: 0,
				letterSpacing: `${fontSpacingTitleBg}`,
				lineHeight: 0.55,
				position: 'absolute',
				right: 0,
				top: 0,
				textTransform: 'uppercase',
				fontWeight: 800,
			},
			hpone: {
				color: '#FFF',				
				display: 'inline-block',
				position: 'relative',
			},
			hptwo: {
				color: '#FBF31D',
				display: 'inline-block',
				position: 'relative',
			},
			divider: {
				overflow: 'inherit',
				background: 'none',
				margin: '65px 0 72px',
				height: 1
			},
			dividerIcon: {
				display: 'inline-block',
				verticalAlign: 'middle',
				color: '#777',
				width: 30,
				height: 30,
				margin: '0 20px'
			},
			outerLine: {
				borderBottom: '1px solid #555',
				width: '25%',
				display: 'inline-block',
				verticalAlign: 'middle',
				content: ' '
			},
			pageTitle: {
				marginTop: 80,
				width: '100%',
				position: 'relative'
			},		
			pageContent: {
				position: 'absolute',
				top: 0,
				left: 0,
				right: 0,
				bottom: 0,
				overflow: 'hidden',
				overflowY: 'auto'
			},
			contentContainer: {
				paddingBottom: 36
			},			
			closeIcon: {
				color: 'rgba(255, 255, 255, 0.5)',
				position: 'absolute',
				top: 35,
				right: 50,
				height: 50,
				width: 50,
				cursor: 'pointer',
				zIndex: 100
			},
			info: {
				marginLeft: 'auto',
				marginRight: 'auto',
				marginBottom: 20
			}
		}

		return(
			<React.Fragment>
				<Slide direction="left" in>
					<div style={styles.main}>
						<div style={styles.pageContent}>
							<div style={styles.pageTitle}>
								<div style={styles.centerAlign}>								
									<span style={styles.titleBg}>resume</span>
			  						<h2 style={styles.headerSection}>
			  							<span style={styles.hpone}>
			  								about
			  							</span>
			  							&nbsp;
			  							<span style={styles.hptwo}>
			  								me
			  							</span>
			  						</h2>
								</div>							
							</div>
							<div style={styles.contentContainer}>
								<div style={styles.centerAlign}>
									<div style={styles.divider}>
										<span style={styles.outerLine}></span>
										<Contact style={styles.dividerIcon}/>
										<span style={styles.outerLine}></span>
									</div>
								</div>
								<div style={styles.info}>

								</div>
							</div>
						</div>
						<CloseIcon style={styles.closeIcon} onClick={this.handleClose}/>
					</div>
				</Slide>
			</React.Fragment>
		);
	}
}


